<template lang="pug">
  .container

    vs-popup(:active.sync="deletePop" title="¿Desea eliminar el item?")
      p Una vez eliminado no se podrá recuperar
      vs-row(vs-type="flex" vs-justify="flex-end")
        vs-button(color="danger" @click="deleteItem") Eliminar

    vs-row(vs-type="flex" vs-justify="space-around")
      vs-col(vs-w="12" vs-type="flex")
        vs-card
          div(slot="header"): h3 Inventario
          template(slot="header")
          div
            vs-table(search :data="items" stripe noDataText="Vacío" max-items="10" pagination)
              template(slot="header"): h3 Items
              template(slot="thead")
                vs-th(sort-key="categoria") Categoría
                vs-th(sort-key="nombre") Nombre
                vs-th(sort-key="cantidad") Cantidad
                vs-th Nota
                vs-th Eliminar
              template(slot-scope="{data}")
                vs-tr(:key="indx" v-for="(itm, indx) in data")
                  vs-td(:data="data[indx].categoria")
                    vs-select(v-model="data[indx].categoria" width="100px" @change="update(data[indx])")
                      vs-select-item(v-for="(cat, indxC) in categorias" :key="indxC" :value="cat.id" :text="cat.nombre")
                  vs-td(:data="data[indx].nombre") {{data[indx].nombre}}
                    template(slot="edit")
                      vs-input(v-model="data[indx].nombre" @change="update(data[indx])")
                  vs-td(:data="data[indx].cantidad") {{data[indx].cantidad}}
                    template(slot="edit")
                      vs-input(v-model="data[indx].cantidad" type="number" :min="1" :step="1" @blur="update(data[indx])")
                  vs-td(:data="data[indx].nota") {{data[indx].nota}}
                    template(slot="edit")
                      vs-input(v-model="data[indx].nota"  @change="update(data[indx])")
                  vs-td
                    vs-button(size="small" icon="delete" color="danger" type="border" @click="deleteItemAttempt(data[indx], indx)")
    vs-card
      div(slot="header"): h3 Agregar Item
      vs-row(vs-justify="space-around")
        vs-select(v-model="newItem.categoria")
          vs-select-item(v-for="(cat, indxC) in categorias" :key="indxC" :value="cat.id" :text="cat.nombre")
        vs-input(placeholder="Nombre" v-model="newItem.nombre")
        vs-input(placeholder="Cantidad" v-model="newItem.cantidad" type="number")
        vs-input(placeholder="Nota" v-model="newItem.nota")
      template(slot="footer")
        vs-row(vs-justify="flex-end")
          vs-button(color="success" @click="addItem" icon="add" size="small")
    vs-card
      div(slot="header"): h3 Agregar Categoría
      vs-row(vs-justify="space-around")
        vs-input(placeholder="Categoría" v-model="newCat.nombre")

      template(slot="footer")
        vs-row(vs-justify="flex-end")
          vs-button(@click="addCat" icon="add" size="small")
</template>

<script>
/* eslint-disable */
import store from '../store.js'
export default {
  name: 'General',
  components: {
    store
  },
  data () {
    return {
      deleteItemObj : null,
      deletePop: false,
      newItem: {
        categoria: 1,
        nombre: "",
        cantidad: 1,
        nota: ""
      },
      newCat: {
        nombre: ""
      },
      newItemDialog: false,
      categorias: [],
      items: []
    }
  },
  methods: {
    getInventario () {
      var _this = this
      var tstamp = new Date().getTime()
      var urly = 'list_items?timestamp='+tstamp
      this.$api.get(urly).then( (response) => {
          Object.keys(response.data).map((key) => {
            _this.items.push(response.data[key])
          })
      })
    },
    update (item) {
      // eslint-disable-next-line
      this.$api.post('update_item', item).then( (response) => {
        //console.log(response.data)
      })
    },
    addItem () {
      if(this.newItem.nombre.length>4){
        this.$api.post('item', this.newItem).then( (response) => {
          this.items.push(response.data)
          this.newItem = { categoria: 1, nombre: "", cantidad: 1, nota: "" }
          this.$vs.notify({ title:"Item añadido", color:'success' })
        })
      } else {
        this.$vs.notify({
          title:"Asigna un nombre de item",
          color:'warning'
        })
      }
    },
    deleteItemAttempt (item, indx) {
      this.deleteItemObj = item
      this.deleteItemIndx = indx
     this.deletePop = true
    },
    deleteItem () {
      this.$api.post('delete_item',this.deleteItemObj).then( (response) => {
        this.$delete(this.items, this.deleteItemIndx)
        this.deletePop = false
      })
    },
    addCat () {
       if(this.newCat.nombre.length>4){
        this.$api.post('categoria', this.newCat).then( (response) => {
          this.categorias.push(response.data)
          this.newCat = { nombre: "" }
          this.$vs.notify({ title:"Categoría añadida" })
        })
      } else {
        this.$vs.notify({
          title:"Asigna un nombre para la categoría",
          color:'warning'
        })
      }
    },
  },
  mounted() {
    this.getInventario()
    this.$api.get('list_categorias').then( (response) => {
      Object.keys(response.data).map((key) => {
        this.categorias.push(response.data[key])
      })
    })
  }
}
</script>

<style scope>
  .vs-table--header{
    padding: 10px;
  }
  .vs-con-table .empty{
    color: #fff;
  }
  .vs-table--search-input{
    border-color: #1F74FF;
  }
</style>
