<template lang="pug">
.container
  vs-row(vs-type="flex" vs-justify="space-around")
    vs-col(vs-w="11" vs-sm="12" vs-type="flex" vs-align="center")
      vs-card
        div(slot="header"): h3.text-center {{hasname}}
        .container
          vs-divider(color="primary").mt.mb Proyecto
          vs-row(vs-type="flex")
            vs-col(vs-w="4"): vs-input(label="Nombre de proyecto" icon="text_fields" v-model="proyecto.nombre" size="large")
            vs-col(vs-w="4"): vs-input(label="Inicio" icon="calendar_today" type="date" v-model="proyecto.fecha_inicio" size="large")
            vs-col(vs-w="4"): vs-input(label="Término" icon="calendar_today" type="date" v-model="proyecto.fecha_termino" size="large")
          vs-divider(color="primary").mt.mb Equipo
          vs-row(vs-type="flex")
            vs-col(vs-w="6")
              .ml.mr: vs-card.equipocard: vs-table(:data="proyecto.itemsEquipo" stripe noDataText="Vacío" max-items="10" pagination)
                template(slot="header"): h3 Equipo para proyecto
                template(slot="thead")
                  vs-th(sort-key="nombre") Nombre
                  vs-th(sort-key="cantidad") Cantidad
                  //vs-th(sort-key="categoria") Categoría
                  vs-th
                template(slot-scope="{data}")
                  vs-tr(:key="indx" v-for="(itm, indx) in data")
                    vs-td(:data="data[indx].nombre"): strong {{data[indx].nombre}}
                    vs-td(:data="data[indx].cantidad") {{data[indx].cantidad}}
                    //vs-td(:data="data[indx].categoria") {{categoryN(data[indx].categoria)}}
                    vs-td(): vs-button(color="danger" @click="quitar(itm)" size="small" icon="keyboard_arrow_right")
            vs-col(vs-w="6")
              vs-card: vs-table(search :data="items" stripe noDataText="Vacío" max-items="10" pagination)
                template(slot="header"): h3 Inventario
                template(slot="thead")
                  vs-th
                  vs-th(sort-key="nombre") Nombre
                  vs-th(sort-key="cantidad") Cantidad
                  //vs-th(sort-key="categoria") Categoría
                template(slot-scope="{data}")
                  vs-tr(:key="indx" v-for="(itm, indx) in data")
                    vs-td(): vs-button(:color="(itm.cantidad > 0 ) ? 'success' : 'warning'" @click="agregar(itm)" :disabled="itm.cantidad==0" size="small" icon="keyboard_arrow_left")
                    vs-td(:data="data[indx].nombre"): strong {{itm.nombre}}
                    vs-td(:data="data[indx].cantidad") {{itm.cantidad}}
                    //vs-td(:data="data[indx].categoria") {{categoryN(itm.categoria)}} 
                    

          vs-row(vs-type="flex" vs-justify="center")
            vs-button(icon="save_alt" size="big" :disabled="!validate" @click="guardar") Guardar Proyecto

        
</template>

<script>
import store from '../store.js'

export default {
  name: 'Nuevo',
  components: {
    store
  },
  data() {
      return{
          proyecto: {
              nombre: null,
              itemsEquipo: []
          },
          categorias: [],
          items: [],
      }
  },
  computed: {
      hasname () {
          if (!this.proyecto.nombre){
              return 'Nuevo Proyecto'
          } else {
              return this.proyecto.nombre
          }
      },
      validate () {
        if(this.proyecto.nombre == null)
          return false
        if(this.proyecto.fecha_inicio == null)
          return false
        if(this.proyecto.fecha_termino == null)
          return false
        if(this.proyecto.itemsEquipo.length == 0)
          return false
        return true
      }
  },
  methods: {
    guardar () {
      this.$vs.notify({ title:"Guardando" })
      this.$api.post('save_project', this.proyecto).then( (response) => {
          if(response.data.result=='ok'){
            this.$router.push('/general')
          }
        })
    },
    agregar (item) {
      const theitem = this.items.find(function(cond){
        return cond.id == item.id
      })
      const qt = theitem.cantidad
      if(qt>0){
        theitem.cantidad = qt-1        
        const itemexists = this.proyecto.itemsEquipo.find(function(cond){
          return cond.id == item.id
        })
        if(itemexists) {
          itemexists.cantidad = itemexists.cantidad+1
        } else {
          var newitem = JSON.parse(JSON.stringify(theitem))
          newitem.cantidad = 1
          this.proyecto.itemsEquipo.push(newitem)
        }
        
      }
    },
    quitar (item) {
      const theitem = this.proyecto.itemsEquipo.find(function(cond){
        return cond.id == item.id
      })
      const theitemindex = this.proyecto.itemsEquipo.findIndex(function(cond){
        return cond.id == item.id
      })
      theitem.cantidad = theitem.cantidad-1        
      const itemexists = this.items.find(function(cond){
        return cond.id == item.id
      })
      itemexists.cantidad = itemexists.cantidad+1
      if(theitem.cantidad==0) {
        this.$delete(this.proyecto.itemsEquipo, theitemindex)
      }
    },
    categoryN(catId) {
      const res =  this.categorias.find(function(cond){
        return cond.id == catId
      })
      if(res){
       return res.nombre
      }
    },
    getInventario () {
      var _this = this
      var tstamp = new Date().getTime()
      var urly = 'list_items?timestamp='+tstamp
      this.$api.get(urly).then( (response) => {
          Object.keys(response.data).map((key) => {
            _this.items.push(response.data[key])
          })
      })
    }
  },
   mounted() {
    this.getInventario()
    this.$api.get('list_categorias').then( (response) => {
      Object.keys(response.data).map((key) => {
        this.categorias.push(response.data[key])
      })
    })
  }
}
</script>

<style scoped>
  .text-center{
    text-align: center;
  }
  .equipocard{
    border: 1px solid #1F74FF;
  }
  .container{
    text-align: left;
  }
  .mt{
    margin-top: 40px;
  }
  .mb{
    margin-bottom: 40px;
  }
  .ml{
    margin-left: 20px;
  }
  .mr{
    margin-right: 20px;
  }
</style>