import Vue from 'vue'
import App from './App.vue'

import axios from 'axios'
// import VueAxios from 'vue-axios'
Vue.use({
  install (Vue) {
  Vue.prototype.$api = axios.create({
    baseURL: store.state.apiurl,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
})

import VueRouter from 'vue-router'

Vue.use(VueRouter)


import Vuesax from 'vuesax'
import 'vuesax/dist/vuesax.css' //Vuesax styles
Vue.use(Vuesax)
Vue.config.productionTip = false
import 'material-icons/iconfont/material-icons.css';

//Routes
import Home from './templates/Home.vue'
import General from './templates/General.vue'
import Inventario from './templates/Inventario.vue'
import Historial from './templates/Historial.vue'
import Ajustes from './templates/Ajustes.vue'
import Nuevo from './templates/Nuevo.vue'
import Finalizar from './templates/Finalizar.vue'




const routes = [
  { path: '/', component: Home },
  { path: '/general', component: General, meta: { requiresAuth:true } },
  { path: '/inventario', component: Inventario, meta: { requiresAuth:true } },
  { path: '/historial', component: Historial, meta: { requiresAuth:true } },
  { path: '/ajustes', component: Ajustes, meta: { requiresAuth:true } },
  { path: '/nuevo', component: Nuevo, meta: { requiresAuth:true } },
  { path: '/finalizar/:id', component: Finalizar, meta: { requiresAuth:true } }
]
const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if(to.meta.requiresAuth){
    if(store.state.user) {
      next()
    } else {
      next('/')
    }
  } else {
    next()
  }
  /*
  if(to.meta.requiredAuth && store.state.user){
    next()
  } else {
    next('/')
  }
  */
})


import store from './store.js'




new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')

