import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

//Store
const store = new Vuex.Store({
  state: {
    user: null,
    apiurl: 'https://dragonbarbudo.com/inventario/'
  },
  mutations: {
    login (state, val) {
      state.user = val
    }
  },
  plugins: [createPersistedState()],
})

export default store