<template lang="pug">
.container
  vs-row(vs-type="flex" vs-justify="space-around")
    vs-col(vs-w="11" vs-sm="12" vs-type="flex" vs-align="center")
      vs-card.centerx
        div(slot="header"): h3 Proyecto Nuevo
        .container
          vs-row(vs-type="flex" vs-justify="center")
            vs-button(icon="add" size="big" to="/nuevo") Iniciar proyecto nuevo

    vs-col(vs-w="5" vs-sm="12" vs-type="flex" v-for="pr in projects" :key="pr.id")
      vs-card
        div(slot="header")
          vs-row
            vs-col(vs-w="6"): h3 {{pr.nombre}}
            vs-col(vs-w="6").text-right
              p: small
                vs-icon(icon="calendar_today" size="12px")
                span  {{pr.inicio}}
                vs-icon(icon="arrow_right_alt" size="12px")
                span  {{pr.termino}}
        vs-list
          vs-list-item(v-for="itm in pr.items_equipo" :key="itm.id" :title="itm.nombre" ) {{itm.cantidad}}
        div(slot="footer")
          vs-row(vs-type="flex" vs-justify="flex-end")
            vs-button(color="success" icon="check" size="small" :to="'/finalizar/'+pr.id") Finalizar

    
</template>

<script>
import store from '../store.js'

export default {
  name: 'General',
  components: {
    store
  },
  data () {
    return {
      projects: []
    }
  },
  methods:  {
    getProjects (){
      var _this = this
      var tstamp = new Date().getTime()
      _this.$api.get('list_projects?timestamp='+tstamp).then( (response) => {
          Object.keys(response.data).map((key) => {
            _this.projects.push(response.data[key])
          })
      })
    }
  },
  mounted() {
    this.getProjects()
  }
}
</script>
<style>
  .text-right{
    text-align: right;
  }
</style>
