<template lang="pug">
  vs-row(vs-align="center" vs-type="flex" vs-justify="space-around")
    vs-col(vs-w="4" vs-xs="10" vs-align="center").centerx
      vs-card.loginForm
        div(slot="header")
          h3 Ingresar
        vs-row(vs-justify="center")
          vs-input(placeholder="Acceso" size="large" v-model="accessKey" maxlength="6")
        div(slot="footer")
          vs-row(vs-justify="flex-end")
            vs-button(type="gradient" color="danger" icon="chevron_right" @click="login") Ingresar
</template>

<script>
import store from '../store.js'

export default {
  data() {
    return {
      store,
      accessKey : null
    }
  },
  methods: {
    login () {
      var _this = this
      if (this.accessKey == null) {
        this.$vs.notify({
          title:"Error",
          text:"Escribe tu clave de acceso | ' inve '",
          color:'danger'
        })
      } else {
        this.$api.post('login', {
          key : _this.accessKey
        }).then( (response) => {
          if(response.data.error){
            this.$vs.notify({
              title:"Error de clave",
              text:"Verifícala o intenta de nuevo",
              color:'danger'
            })
          } else {
            store.commit('login', response.data)
            this.$vs.notify({
              title:"Bienvenido",
              color:'success'
            })
            this.$router.push('/general')
          }
        })
      }
    }
  }
}
</script>

<style scoped>
  input{
    text-align: center;
  }
  .loginForm{
    margin-top: 80px;
  }
</style>
