<template lang="pug">
.container
  vs-row(vs-type="flex" vs-justify="space-around")
    vs-col(vs-w="11" vs-sm="12" vs-type="flex" vs-align="center")
      vs-card
        div(slot="header"): h3.text-center {{proyecto.nombre}}
        .container
          vs-divider(color="primary").mt.mb Equipo utilizado
          vs-row(vs-type="flex" vs-justify="space-between")
            vs-col(vs-w="5" vs-sm="12" vs-type="flex" v-for="itm in proyecto.items_equipo" :key="itm.id")
              vs-card
                div(slot="header")
                  vs-row
                    vs-col(vs-w="10"): h3 {{itm.nombre}}
                    vs-col(vs-w="2").text-right {{itm.cantidad}}
                div(slot="footer")
                  vs-row(vs-type="flex" vs-justify="space-between")
                    vs-button(color="danger" icon="settings" @click="mandarA(itm, 'mantenimiento')") A mantenimiento
                    vs-button(color="success" icon="check" @click="mandarA(itm, 'inventario')") A inventario
          
          vs-divider(color="danger").mt.mb Mantenimiento
          vs-row(vs-type="flex" vs-justify="space-between")
            vs-col(vs-w="5" vs-sm="12" vs-type="flex" v-for="(itm, index) in proyecto.items_regresar" :key="index" v-if="itm.ruta=='mantenimiento'")
              vs-card.a-mantenimiento
                div(slot="header"): h3 {{itm.nombre}}
                //div(slot="footer")
                  vs-row(vs-type="flex" vs-justify="flex-end")
                    vs-button(color="success" icon="check" size="small" @click="moverA(itm,'inventario')")
                    p {{itm.ruta}}
          vs-divider(color="success").mt.mb Regreso a inventario
          vs-row(vs-type="flex" vs-justify="space-between")
            vs-col(vs-w="5" vs-sm="12" vs-type="flex" v-for="(itm, index) in proyecto.items_regresar" :key="index" v-if="itm.ruta=='inventario'")
              vs-card.a-inventario
                div(slot="header"): h3 {{itm.nombre}}
                //div(slot="footer")
                  vs-row(vs-type="flex" vs-justify="flex-end")
                    vs-button(color="danger" icon="settings" size="small" @click="moverA(itm, 'mantenimiento')")
                    p {{itm.ruta}}
        //.container: vs-row(vs-type="flex" vs-justify="center")
          vs-button(icon="save_alt" size="big" :disabled="!validate" @click="guardar") Guardar Proyecto
 
</template>

<script>
import store from '../store.js'

export default {
  name: 'Nuevo',
  components: {
    store
  },
  data() {
      return{
          proyecto: {
              nombre: null,
              items_equipo: [],
              items_regresar: [],
          },
          categorias: [],
          items: [],
      }
  },
  computed: {
      validate () {
        if(this.proyecto.nombre == null)
          return false
        if(this.proyecto.fecha_inicio == null)
          return false
        if(this.proyecto.fecha_termino == null)
          return false
        if(this.proyecto.items_equipo.length == 0)
          return false
        return true
      }
  },
  methods: {
    moverA(item, sendTo) {
      const theitemindex = this.proyecto.items_regresar.findIndex(function(cond){
        return cond.id == item.id
      })
      this.$delete(this.proyecto.items_regresar, theitemindex)
      const nuitm = {
        items_id: item.items_id,
        last_project_id: item.project_id,
        nombre: item.nombre,
        ruta: sendTo
      }
      this.proyecto.items_regresar.push(nuitm)
    },
    mandarA (item, sendTo) {
      const theitem = this.proyecto.items_equipo.find(function(cond){
        return cond.id == item.id
      })
      const theitemindex = this.proyecto.items_equipo.findIndex(function(cond){
        return cond.id == item.id
      })
      theitem.cantidad = theitem.cantidad - 1
      if(theitem.cantidad==0) {
        this.$delete(this.proyecto.items_equipo, theitemindex)
      }
      const moveitem = {
        items_id: item.items_id,
        last_project_id: item.project_id,
        nombre: item.nombre,
        ruta: sendTo
      }
      this.proyecto.items_regresar.push(moveitem)
    },
    guardar () {
      this.$vs.notify({ title:"Guardando" })
      this.$api.post('save_project', this.proyecto).then( (response) => {
          if(response.data.result=='ok'){
            this.$router.push('/general')
          }
        })
    },
    agregar (item) {
      const theitem = this.items.find(function(cond){
        return cond.id == item.id
      })
      const qt = theitem.cantidad
      if(qt>0){
        theitem.cantidad = qt-1        
        const itemexists = this.proyecto.items_equipo.find(function(cond){
          return cond.id == item.id
        })
        if(itemexists) {
          itemexists.cantidad = itemexists.cantidad+1
        } else {
          var newitem = JSON.parse(JSON.stringify(theitem))
          newitem.cantidad = 1
          this.proyecto.items_equipo.push(newitem)
        }
        
      }
    },
    quitar (item) {
      const theitem = this.proyecto.items_equipo.find(function(cond){
        return cond.id == item.id
      })
      const theitemindex = this.proyecto.items_equipo.findIndex(function(cond){
        return cond.id == item.id
      })
      theitem.cantidad = theitem.cantidad-1        
      const itemexists = this.items.find(function(cond){
        return cond.id == item.id
      })
      itemexists.cantidad = itemexists.cantidad+1
      if(theitem.cantidad==0) {
        this.$delete(this.proyecto.items_equipo, theitemindex)
      }
    },
    categoryN(catId) {
      const res =  this.categorias.find(function(cond){
        return cond.id == catId
      })
      if(res){
       return res.nombre
      }
    },
    getInventario () {
      var _this = this
      var tstamp = new Date().getTime()
      var urly = 'list_items?timestamp='+tstamp
      this.$api.get(urly).then( (response) => {
          Object.keys(response.data).map((key) => {
            _this.items.push(response.data[key])
          })
      })
    },
    getProject () {
        var _this = this
        var routeid = this.$route.params.id
        this.$api.post('project', {id:routeid}).then( (response) => {
            _this.proyecto = response.data
            _this.proyecto.items_regresar = []
        })
    }
  },
   mounted() {
    this.getProject()
    
    this.getInventario()
    this.$api.get('list_categorias').then( (response) => {
      Object.keys(response.data).map((key) => {
        this.categorias.push(response.data[key])
      })
    })
  }
}
</script>

<style scoped>
  .text-center{
    text-align: center;
  }
  .a-mantenimiento{
    border: 1px solid #FF4757;
  }
  .a-inventario{
    border: 1px solid #46C93A;
  }
  .container{
    text-align: left;
  }
  .mt{
    margin-top: 40px;
  }
  .mb{
    margin-bottom: 40px;
  }
  .ml{
    margin-left: 20px;
  }
  .mr{
    margin-right: 20px;
  }
</style>